import React from 'react';
import { v4 as uuid } from 'uuid';

class UpdateForm extends React.Component {
	state = {
		id: this.props.toUpdate.id,
		name: this.props.toUpdate.name,
		price: this.props.toUpdate.price,
		category: this.props.toUpdate.category,
		image: this.props.toUpdate.image,
	};

	saveUpdate = () => {
		// console.table(this.state);
		let updatedItem = {
			id: this.state.id,
			name: this.state.name,
			price: this.state.price,
			category: this.state.category,
			image: this.state.image,
		};
		this.props.saveUpdate(updatedItem);
	};
	cancelUpdateItem = () => {
		this.props.toHideUpdateForm();
		this.setState({
			name: '',
			price: '',
			category: '',
			image: '',
		});
	};
	render() {
		return (
			<div className='update-container'>
				<div>
					<div className='update-item-container clearfix'>
						<div className='Images'>
							<img src={this.state.image} alt={this.state.image} />
						</div>
						<div className='desc'>
							<strong className='text'>{this.state.name}</strong>
							<p className='text'>
								<small>{this.state.price}</small>
							</p>
							<p className='text'>
								<small>{this.state.category}</small>
							</p>
						</div>
					</div>
				</div>
				<div className='form-container-update'>
					Name:{' '}
					<input
						type='text'
						value={this.state.name}
						onChange={(e) => {
							this.setState({ name: e.target.value });
						}}
					/>
					<br />
					Price:{' '}
					<input
						type='number'
						value={this.state.price}
						onChange={(e) => {
							this.setState({ price: e.target.value * 1 });
						}}
					/>
					<br />
					Category:
					<select
						className='select-add'
						value={this.state.categories}
						onChange={(e) => {
							this.setState({ category: e.target.value });
						}}
					>
						{this.props.categories.map((cat) => {
							return <option>{cat}</option>;
						})}
					</select>
					<br />
					Image:{' '}
					<input
						type='text'
						value={this.state.image}
						onChange={(e) => {
							this.setState({ image: e.target.value });
						}}
					/>
					<br />
					<div>
						<button className='btn' onClick={this.saveUpdate}>
							Update
						</button>
						<button className='btn' onClick={this.cancelUpdateItem}>
							Cancel
						</button>
					</div>
				</div>
			</div>
		);
	}
}

export default UpdateForm;
