import React from 'react';
import './ItemBox.css';

class ItemBox extends React.Component {
	state = {
		quantity: 1,
	};

	addOrder = () => {
		this.props.addToCart(this.props.item, this.state.quantity);
	};

	deleteHandler = () => {
		this.props.deleteItem(this.props.item.name);
	};
	updateHandler = () => {
		this.props.toUpdateItem(this.props.item.id);
	};

	render() {
		return (
			<>
				<div className='item-card clearfix'>
					<div className='Images'>
						<img src={this.props.item.image} alt={this.props.item.image} />
					</div>
					<div className='desc'>
						<strong className='text'>{this.props.item.name}</strong>
						<p className='text'>
							<small>{this.props.item.price}</small>
						</p>
						<p>
							{this.props.userRole === 'admin' ? (
								<>
									<button onClick={this.deleteHandler}>Delete</button>
									<button onClick={this.updateHandler}>Update</button>
								</>
							) : (
								<>
									<input
										className='add-order-qty'
										type='number'
										min='1'
										value={this.state.quantity}
										onChange={(e) => {
											this.setState({ quantity: e.target.value * 1 });
										}}
									/>
									<button onClick={this.addOrder}>Order</button>
								</>
							)}
						</p>
					</div>
				</div>
			</>
		);
	}
}

export default ItemBox;
