import React from 'react';

class Cart extends React.Component {
	render() {
		return (
			<div>
				<img
					width='50'
					src={this.props.item.image}
					alt={this.props.item.name}
				/>
				{this.props.item.name} x {this.props.item.quantity}
			</div>
		);
	}
}

export default Cart;
