import React from 'react';
import { v4 as uuid } from 'uuid';

class AddItemForm extends React.Component {
	state = {
		name: '',
		price: 0,
		category: this.props.categories[0],
		image: '',
		ShowAddForm: false,
	};

	addNewItem = () => {
		// console.table(this.state);
		let newItem = {
			id: uuid(),
			name: this.state.name,
			price: this.state.price,
			category: this.state.category,
			image: this.state.image,
		};
		this.props.addNewItem(newItem);
	};
	cancelAddItem = () => {
		this.setState({
			ShowAddForm: false,
		});
	};

	btnShowAdd = () => {
		this.setState({
			ShowAddForm: true,
		});
	};

	render() {
		return (
			<>
				{this.state.ShowAddForm === true ? (
					<div className='form-container'>
						Name:{' '}
						<input
							className='add-item-input'
							type='text'
							value={this.state.name}
							onChange={(e) => {
								this.setState({ name: e.target.value });
							}}
						/>
						<br />
						Price:{' '}
						<input
							className='add-item-input'
							type='number'
							value={this.state.price}
							onChange={(e) => {
								this.setState({ price: e.target.value * 1 });
							}}
						/>
						<br />
						Category:
						<select
							className='select-add'
							value={this.state.categories}
							onChange={(e) => {
								this.setState({ category: e.target.value });
							}}
						>
							{this.props.categories.map((cat) => {
								return <option>{cat}</option>;
							})}
						</select>
						<br />
						Image:{' '}
						<input
							className='add-item-input'
							type='text'
							value={this.state.image}
							onChange={(e) => {
								this.setState({ image: e.target.value });
							}}
						/>
						<br />
						<div>
							<button className='btn' onClick={this.addNewItem}>
								Save
							</button>
							<button className='btn' onClick={this.cancelAddItem}>
								Cancel
							</button>
						</div>
					</div>
				) : (
					<button className='btn' onClick={this.btnShowAdd}>
						Add new Item
					</button>
				)}
			</>
		);
	}
}

export default AddItemForm;
