import React from 'react';
import './NavBar.css';

class NavBar extends React.Component {
	adminSide = () => {
		this.props.adminSide();
	};
	clientSide = () => {
		this.props.clientSide();
	};
	render() {
		return (
			<>
				<nav>
					<ul>
						<li>
							<button onClick={this.adminSide}>Admin Side</button>
						</li>
						<li>
							<button onClick={this.clientSide}>Client Side</button>
						</li>
					</ul>
				</nav>
			</>
		);
	}
}

export default NavBar;
