import './App.css';
import React from 'react';
import ItemBox from './ItemBox';
import './ItemBox.css';
import { v4 as uuid } from 'uuid';
import AddItemForm from './AddItemForm';
import NavBar from './NavBar';
import Cart from './Cart';
import UpdateForm from './UpdateForm';

class App extends React.Component {
	state = {
		items: [
			{
				id: uuid(),
				name: 'Burger',
				price: 50,
				category: 'Food',
				image: 'https://image.flaticon.com/icons/svg/1046/1046784.svg',
			},
			{
				id: uuid(),
				name: 'Pizza',
				price: 100,
				category: 'Food',
				image: 'https://image.flaticon.com/icons/svg/1046/1046771.svg',
			},
			{
				id: uuid(),
				name: 'Fries',
				price: 25,
				category: 'Food',
				image: 'https://image.flaticon.com/icons/svg/1046/1046786.svg',
			},
			{
				id: uuid(),
				name: 'Coffee',
				price: 35,
				category: 'Drink',
				image: 'https://image.flaticon.com/icons/svg/1046/1046785.svg',
			},
			{
				id: uuid(),
				name: 'Iced Tea',
				price: 45,
				category: 'Drink',
				image: 'https://image.flaticon.com/icons/svg/1046/1046782.svg',
			},
			{
				id: uuid(),
				name: 'Hot Tea',
				price: 45,
				category: 'Drink',
				image: 'https://image.flaticon.com/icons/svg/1046/1046792.svg',
			},
		],
		category: 'All',
		// displayItems: [],
		categories: ['Drink', 'Food', 'Ice Cream'],

		userRole: 'admin',

		cart: [
			{
				name: 'Hot Tea',
				price: 45,
				category: 'Drink',
				image: 'https://image.flaticon.com/icons/svg/1046/1046792.svg',
				quantity: 2,
			},
			{
				name: 'Iced Tea',
				price: 45,
				category: 'Drink',
				image: 'https://image.flaticon.com/icons/svg/1046/1046782.svg',
				quantity: 5,
			},
		],

		toUpdate: null,
	};
	/* categoryFilter = (cat) =>{
    let category = this.state.category.slice(0);
    category.push(cat);
    this.setState({
      category: category
    })

  }; */

	// componentDidMount() {
	// 	this.setState({
	// 		displayItems: this.state.items,
	// 	});
	// }

	categoryDropDown = (chosenCategory) => {
		let itemsCopy = this.state.items.slice(0);

		// if (this.state.categories.includes(chosenCategory)) {
		// 	itemsCopy = itemsCopy.filter((item) => {
		// 		return item.category === chosenCategory;
		// 	});
		// }

		this.setState({
			category: chosenCategory,
			// displayItems: itemsCopy,
		});
	};

	addNewItem = (newItem) => {
		let itemsCopy = this.state.items.slice(0);
		itemsCopy.push(newItem);

		let newDisplayItems = itemsCopy.filter((item) => {
			return item.category === newItem.category;
		});
		// console.table(itemsCopy);
		this.setState({
			items: itemsCopy,
			category: newItem.category,
		});
		// alert(`${newItem.name} was succesfully added`);
	};

	deleteItem = (itemName) => {
		let updatedItems = this.state.items.filter((item) => {
			return item.name !== itemName;
		});

		this.setState({
			items: updatedItems,
		});
		alert(itemName + ' was deleted');
	};

	adminSide = () => {
		this.setState({
			userRole: 'admin',
		});
	};

	clientSide = () => {
		this.setState({
			userRole: 'staff',
		});
	};
	toUpdateItem = (id) => {
		// alert(id);
		let toUpdateCopy = this.state.items.slice(0);
		for (let x = 0; x < toUpdateCopy.length; x++) {
			if (toUpdateCopy[x].id === id) {
				this.setState({
					toUpdate: toUpdateCopy[x],
				});
			}
		}
	};
	toHideUpdateForm = () => {
		this.setState({
			toUpdate: null,
		});
	};
	saveUpdate = (itemToUpdate) => {
		let displayUdpatedItems = this.state.items.filter(
			(item) => item.id !== itemToUpdate.id
		);
		displayUdpatedItems.push(itemToUpdate);
		this.setState({
			items: displayUdpatedItems,
			toUpdate: null,
		});
	};

	addToCart = (item, quantity) => {
		// look for ordered item in cart
		// if item is in cart, change quantity
		// if item not in cart, push

		let cartCopy = this.state.cart.slice(0);
		let found = false;
		for (let x = 0; x < cartCopy.length; x++) {
			if (cartCopy[x].name === item.name) {
				cartCopy[x].quantity += quantity;

				found = true;

				break;
			}
		}

		if (!found) {
			let myCart = {
				name: item.name,
				image: item.image,
				price: item.price,
				category: item.category,
				quantity: quantity,
			};
			cartCopy.push(myCart);
		}

		this.setState({
			cart: cartCopy,
		});
	};

	render() {
		let displayItems = this.state.items;
		if (this.state.category !== 'All') {
			displayItems = this.state.items.filter((item) => {
				return item.category === this.state.category;
			});
		}
		return (
			<div className='App'>
				<NavBar adminSide={this.adminSide} clientSide={this.clientSide} />
				{this.state.toUpdate === null ? (
					<>
						{this.state.userRole === 'admin' ? (
							<div className='add-container'>
								<AddItemForm
									categories={this.state.categories}
									addNewItem={this.addNewItem}
									categoryDropDown={this.categoryDropDown}
								/>
							</div>
						) : (
							''
						)}
						<hr />
						<div className='App-header'>
							<select
								className='select-filter'
								value={this.state.category}
								onChange={(e) => this.categoryDropDown(e.target.value)}
							>
								<option>All</option>
								{this.state.categories.map((category) => {
									return <option>{category}</option>;
								})}
							</select>
							<div className='container clearfix'>
								<div className=' clearfix items-container '>
									{displayItems.map((item) => {
										return (
											<ItemBox
												key={item.id}
												item={item}
												deleteItem={this.deleteItem}
												userRole={this.state.userRole}
												addToCart={this.addToCart}
												toUpdateItem={this.toUpdateItem}
											/>
										);
									})}
								</div>
								{this.state.userRole !== 'admin' ? (
									<div className='cart'>
										<h2>Cart</h2>
										{this.state.cart.map((item) => {
											return <Cart item={item} />;
										})}
									</div>
								) : (
									''
								)}
							</div>
						</div>
					</>
				) : (
					<div className='add-container'>
						<UpdateForm
							categories={this.state.categories}
							toUpdate={this.state.toUpdate}
							toHideUpdateForm={this.toHideUpdateForm}
							saveUpdate={this.saveUpdate}
						/>
					</div>
				)}
			</div>
		);
	}
}
export default App;
